.table-item-container {
    display: flex;
    flex-direction: column;
    overflow: auto;
  }
  
  .table-item-container > div {
    flex: 1;
  }
  
  .table-item-cell-label {
    display: flex;
    align-items: center;
    padding: 7px;
    font-size: 13px;
    height: 100%;
  }
  
  .table-item-cell-data {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .table-item-cell-block {
    padding: 0;
    position: relative;
    border: 1px solid #eeeeee;
  }
  
  .table-header-cell {
    font-size: 13px;
    padding: 7px;
    background-color: #eeeeee;
  }
  
  .table-title-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  
  .table-item-title {
    font-size: 16px;
  }
  
  .legend-definition-list,
  .legend-definition-item {
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  
  .legend-definition-color {
    height: 30px;
    width: 50px;
    border: solid thin #dddddd;
  }
  
  .legend-definition-name {
    font-size: 12px;
    margin-left: 10px;
  }
  
  .reshuffle-icon {
    padding: 0;
    display: flex;
  }
  .reshuffle-icon img {
    height: 22px;
  }
  
  .table-header-item {
    display: flex;
    align-items: center;
    height: 100%;
  }
  